// Скрипт для создание эффекта появления/скрытия информации (спойлеры)

document.querySelector(document).ready(function() {
    document.querySelector('.first-level').click(function(event) {
        if(document.querySelector('.sidenav__link').classList.contains('one')){
            document.querySelector('.first-level').not(document.querySelector(this)).classList.remove('active');
            document.querySelector('.second-level').not(document.querySelector(this).nextElementSibling).slideUp(300);
        }
        document.querySelector(this).classList.toggle('active').nextElementSibling.slideToggle(300);
    });
});